*{box-sizing: border-box;
}
img{max-width: 100%;}
/*section1*/
.grid1{
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  padding:30px;
}
.grid1 div button{
  background-color: darkkhaki;
  font-size: medium;
  font-style: oblique;
  font-weight: bold;
  width: 150px;
  padding: 10px;
  margin-top: 20px;
  border: none;
  border-radius: 10%;
}
.grid1 div button:hover{cursor: grab;}
/*section2*/
.buttons{
  text-align: center;
}
.buttons button{    
  padding: 20px;
  margin: 20px;
  border: none;
  border-radius: 10%;
  background-color: rgb(54, 175, 200);
}
.buttons button:hover{cursor: grab;}
/*section3*/
.grid2 img{width:70%}
.grid2{
  padding-top: 10px;
  background-color: lightslategrey;
  text-align: center;
  display: grid;
  grid-template-columns: auto auto auto;
}
.grid2 p{
  font-weight: bold;
  font-style: italic;
}
/*section3*/
.grid3{
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-around;
}
.grid3 p{
  font-size: medium;
  font-weight: bold;
}
/*footer*/
.copy{
  background-color: black;
  text-align: center;
  color: aliceblue;
  padding: 20px;
}